.trial-card{
    border-radius: 1rem;
    margin: calc($separacion * 2) 0;
    background-color: $colorPrincipal;
    display: grid;
    grid-template-columns: 45% 55%;
    align-items: center;
    padding: $separacion $separacion 0 $separacion;
    @include telefono{
        margin: $separacion 0;
        display: flex;
        flex-direction: column;
        padding: calc($separacion / 2) calc($separacion / 2) 0 calc($separacion / 2);
    }
    .trial-card__info{
        padding-right: calc($separacion / 2);
        .trial-card__title{
            color: $blanco;
            font-size: 3.5rem;
            @include telefono{
                font-size: 3rem;
            }
        }
        .trial-card__info{
            color: $blanco;
            font-weight: $light;
            margin: 1rem 0 3.25rem 0;
            @include telefono{
                padding: 0
            }
        }
        .btn{
            @include btnWhite;
        }
    }
    .trial-card__img{
        width: 100%;
        height: 100%;
        @include telefono{
            margin-top: calc($separacion * 1.5);
        }
        .trial-card__image{
            height: 100%;
        }
    }
}